import axios, { AxiosResponse } from 'axios';
// const axios = require('axios');
const API_DOMAIN = 'https://604b401bee7cb900176a1a9e.mockapi.io';
export class ApiService {
    constructor() {
        axios.defaults.baseURL = `${API_DOMAIN}/api/v1/`;
    }
    get(url) {
        return new Promise((resolve,reject) => {
            axios.get(url)
            .then((res)=> {
                console.log(res && res.data);
                return(
                    resolve(res.data)
                )
            })
            .catch((error) => {
                reject(error);
            })
        })
    }
}
export default ApiService;
