import React,{Component, useEffect, useRef} from 'react';
import {ApiService} from '../Api/getApi';
const api = new ApiService();
class Product extends Component {
	constructor(props) {
		super(props)
		this.state = {
				product : []
		}
	}
	async componentDidMount() {
		const product = await api.get('products')
		if (!product) {
			console.log('loading');
		} else if (product.length === 0) {
			return <p>empty</p>;
		}else {
			return this.setState({product: product});
		}
	}

	componentDidUpdate() {
		console.log('update');
	}

	render() {
		const {product} = this.state;
		return(
				<div className="product">
					{
						product && (
							<>
							<List data={product}/>
							</>
						)
					}
				</div>
		)
	}
}
const List = ({data}) => {
	const refContainer = useRef(null);
	useEffect(() => {
		console.log('ref', refContainer);
	},[]);
	const style = {
		color:'#ffb25d'
	}
 	const generalRatingStar = (stars) => {
		switch(stars) {
			case '5' :
				return(
					<>
					<label class = "full" style={style} for="activestar5" id="1"></label>
					<label class = "full" for="activestar4" style={style} id="2"></label>
					<label class = "full" for="activestar3" style={style} id="3"></label>
					<label class = "full" for="activestar2" style={style} id="4"></label>
					<label class = "full" for="activestar1" style={style} id="5"></label>
					</>
				)
			case '4' :
				return(
					<>
					<label class = "full" for="activestar5" style={style} id="1"></label>
					<label class = "full" for="activestar4" style={style} id="2"></label>
					<label class = "full" for="activestar3" style={style} id="3"></label>
					<label class = "full" for="activestar2" style={style} id="4"></label>
					<label class = "full" for="activestar1" id="5"></label>
					</>
				)
			case '3' :
				return(
					<>
					<label class = "full" for="activestar5" style={style} id="1"></label>
					<label class = "full" for="activestar4" style={style} id="2"></label>
					<label class = "full" for="activestar3" style={style} id="3"></label>
					<label class = "full" for="activestar2"  id="4"></label>
					<label class = "full" for="activestar1"  id="5"></label>
					</>
				)
			case '2':
				return(
					<>
					<label class = "full" for="activestar5" style={style} id="1"></label>
					<label class = "full" for="activestar4" style={style} id="2"></label>
					<label class = "full" for="activestar3" id="3"></label>
					<label class = "full" for="activestar2" id="4"></label>
					<label class = "full" for="activestar1"  id="5"></label>
					</>
				)
			case '1' :
				return(
					<>
					<label class = "full" for="activestar5" style={style} id="1"></label>
					<label class = "full" for="activestar4" id="2"></label>
					<label class = "full" for="activestar3" id="3"></label>
					<label class = "full" for="activestar2" id="4"></label>
					<label class = "full" for="activestar1" id="5"></label>
					</>
				)
			case '' :
				return(
					<>
					<label class = "full" for="activestar5" id="1"></label>
					<label class = "full" for="activestar4" id="2"></label>
					<label class = "full" for="activestar3" id="3"></label>
					<label class = "full" for="activestar2" id="4"></label>
					<label class = "full" for="activestar1" id="5"></label>
					</>
				)
			default :
				break;
		}	
	}

	return(
		<ul className="list-product">
		{data.map((item, index) => (
		<li className="list-product-item" key={index}>
				<div className="wrapper-contents">
					<figure className="left text-center"><img className="item-img" src={item.cover}/></figure>
					<div className="right">
						<span className="title text-left">{item.name}</span>
						{
							+item.sales == 0 ? (
								<span className="price">${item.price}</span>
							) :
							<>
							<span className="price cancel">${item.price}</span>
							<span className="sales">
							{
								+item.sales != 0 &&
								(percentCalculation(item.sales, item.price))
							}
							</span>
							</>
						}
						{	
							item.stars && 
							(
								<fieldset ref={refContainer} class="rating">
									{generalRatingStar(item.stars)}
								</fieldset>
							)
						}
						
					</div>
				</div>
				<button onClick= {() => {addItem(item.id)}}>Add to cart</button>
		</li>
		))}
		</ul>
	)
};
const percentCalculation = (total,price) => {
	if (total.indexOf('%') == -1)  {
		const priceFormat = new Intl.NumberFormat('en-CA', 
    {style: 'currency', currency:'CAD'}).format(parseFloat(price) + parseFloat(total));
		return priceFormat;
	}else {
		const rlTotal = total.replace(/[-/\\^$*+?.()|[\]{}%]/g,'');
		return  '$' + (parseFloat(price)-(parseFloat(price) * parseFloat(rlTotal))/100);
	}
}

const addItem = (id) => {
	console.log(id);
}
export default Product;
